import React from "react"
import { graphql, useStaticQuery } from 'gatsby'
import * as S from "./styled"


const Profile = () => {
  const { site } = useStaticQuery(graphql`
    query MyQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <S.PageWrapper>
        <S.FirstGroupWrapper>
          <S.Title>{site.siteMetadata.title}</S.Title>
        </S.FirstGroupWrapper>
        <S.SecondGroupWrapper>
          <S.ProfileWrapper>
            <p>
              Im an author, YouTuber, musician, and podcaster who is passionate about helping people become more capable and productive.
            </p>

            <p>
              Most of my work today is done at <a href="https://collegeinfogeek.com" target="_blank" rel="noopener noreferrer">College Info Geek</a>  a site I created in 2010 in order to share my experiments in becoming a more effective student.
            </p>

            <p>
              Today, College Info Geek is one of the worlds largest and best-loved resources for students, and includes a blog, podcast, and a YouTube channel with over 1.7 million subscribers.
            </p>
          </S.ProfileWrapper>
        </S.SecondGroupWrapper>
    </S.PageWrapper>
  )
}

export default Profile
